@import './colors';

.ant-table-wrapper {
  width: 100%;
  table {
    margin-bottom: 2.05rem;
    @include screen('desktop') {
      margin-bottom: 1.5rem;
    }
    @include screen('landscape') {
      margin-bottom: 25px;
      overflow-x: scroll;
      width: 700px;
    }
    border-collapse: collapse;
    .ant-table-thead {
      > tr {
        > th {
          @include font-size(16);
          font-weight: 500;
          background-color: #F2F0F0;
          border-color: #e2e2e3;
          white-space: nowrap;
          padding-top: 20px;
          padding-bottom: 20px;
          padding-left: 1.5rem;
          line-height: 1.2rem;
          @include screen('tablet') {
            @include font-size($size: 12, $base: 16);
          }
        }
      }
    }
    .ant-table-tbody {
      > tr {
        // &:last-child{
        //   td{
        //     border-bottom: 0;
        //   }
        // }
        > td {
          @include font-size(16);
          border-color: #E0E0E0;
          padding-top: 14px;
          padding-bottom: 13px;
          color: $text-default;
          line-height: 1.2rem;
          padding-left: 1.5rem;
          @include screen('md-desktop') {
            padding-top: 20px;
            padding-bottom: 20px;
          }
          @include screen('tablet') {
            @include font-size($size: 14, $base: 16);
          }
        }
      }
    }
  }
}
