// Pagination

.ant-pagination-prev .ant-pagination-item-link,
.ant-pagination-next .ant-pagination-item-link,
.ant-pagination-prev,
.ant-pagination-next,
.ant-pagination-item,
.ant-pagination-item a{
  // display: block;
  height: 2.3rem;
  width: 2.3rem;
  line-height: 2.3rem;
  @include border-radius(4px);
}
.ant-pagination-options .ant-select-single:not(.ant-select-customize-input) .ant-select-selector{
  height: 2.3rem;
 }
 .ant-pagination-options .ant-select-single:not(.ant-select-customize-input) .ant-select-selector .ant-select-selection-item{
  line-height: 2.3rem;
}
.ant-pagination-next .ant-pagination-item-link span{
  @include font-size(17);
}
.ant-pagination-item-active a{
  @include font-size(17);
  color: $white-color;
  // line-height: 1.3rem;
  background-color: $primary-color;
}
